.units_new {
    &_banner {
        display: flex;
        background-image: url(../img/mans/love/love_unitsBg.webp);
        background-size: 100% 100%;
        width: 100%;
        height: 480px;
        align-items: center;
        margin-bottom: 100px;
        @media (max-width: 960px) {
            background-image: url(../img/mans/love/love_untsMB.webp);
            background-size: 100% 100%;
            align-items: flex-end;
        }
        @media (max-width: 560px) {
            background-size: cover;
            background-position-x: 500px;
        }
        &_log {
            color: black;
            padding-left: 150px;
            text-transform: uppercase;
            font-size: 42px;
            @media (max-width: 560px) {
                padding-left: 40px;
            }
        }
    }
    .units_product {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        gap: 15px;
        max-width: 1600px;
        margin: 0 auto;
        &_block {
            width: 383px;
            height: auto;
            margin-bottom: 15px;
            &_photo {
                width: 383px;
                height: 383px;
            }
            .units_block {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                padding-left: 10px;
                margin-top: -30px;
                &_price {
                    background-color: white;
                    width: auto;
                    padding: 3px 5px;
                }
                &_title {
                    padding-top: 10px;
                }
                &_category {
                    color: rgb(170, 170, 170);
                }
                &_count {
                }
                p {
                    margin-block-start: 0;
                    margin-block-end: 0;
                    font-size: 15px;
                }
            }
        }
    }
    .units_content {
        max-width: 1600px;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        img {
            width: 55%;
            display: flex;
            align-items: flex-start;
        }
        &_to {
            width: 45%;
            display: flex;
            align-items: flex-start;
            justify-content: flex-end;
            flex-direction: column;
            padding: 20px 25px;
            background-color: white;
            p {
                text-align: left;
                font-size: 17px;
                margin: 0;
            }
            &_title {
                padding-bottom: 10px;
                font-weight: bold;
            }
            &_info {
                padding-bottom: 25px;
            }
            .units_link {
                color: black;
                text-decoration: none;
                padding: 2px;
            }
            .units_link:hover {
                background-color: black;
                color: white;
                transition: all 0.6s ease-out;
            }
        }
    }
}
